*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: 'Aricum Variable Concept';
  src: url(/gamestation/static/media/AcuminVariableConcept.f96253aa.otf);
}

body {
  margin: 0;
  font-family: 'Aricum Variable Concept';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root {
  --pantone-1: #8E73D8;
  --pantone-2: #1F0459;
  --gray-1: #E6E6E6;
  --gray-2: #808080;
  --gray-3: #F2F2F2;
  --gray-4: #666666;

  /* Color Select */

  --color-1: #8E73D8;
  --color-2: #1F0459;
  --color-3: #0D0126;
  --color-4: #9305F2;
  --color-5: #3FA9F5;
  --color-6: #7AC943;
  --color-7: #FF1D25;

  --success: #006837;
  --danger: #C1272D;

}
.background {
    background: url(/gamestation/static/media/login-background.ba486041.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

form {
    font-family: 'Aricum Variable Concept';
    background-color: rgba(255, 255, 255);
    padding: 40px;
    border-radius: 0 0 10px 10px;
    border-top: 10px var(--pantone-1) solid;
    box-shadow: 0 0 15px black;
    display: flex;
    flex-direction: column;
    width: 35%;
    text-align: center;
    position: relative;
}

form h4 {
    font-size: 35px;
    letter-spacing: 1px;
    font-weight: 400;
}

form h4 span{
    font-weight: bolder;
    color: var(--pantone-1);
}

form input {
    height: 40px;
    font-size: 18px;
    padding: 10px;
    border: none;
    border-bottom: gray 4px solid;
    margin: 20px 0;
    outline: none;
}

form input:not(:placeholder-shown), form input:focus{
    border-bottom: var(--pantone-1) 4px solid;
    outline: none;
}

form button {
    border: none;
    background-color: var(--pantone-1);
    height: 50px;
    color: white;
    font-size: 18px;
}

form button:hover {
    background-color: var(--pantone-2);
    cursor: pointer;
}

.alert {
    display: flex;
    -webkit-animation-name: fadeout;
            animation-name: fadeout;
    -webkit-animation-duration: 5500ms;
            animation-duration: 5500ms;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    position: absolute;
    top: 60px;
    left: auto;
    width: 50%;
    height: 50px;
    background-color: var(--pantone-1);
    color: white;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    z-index: 10000;
}

@-webkit-keyframes fadeout {
    0% {
        opacity: 1;
    }
    75% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeout {
    0% {
        opacity: 1;
    }
    75% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.container {
    width: 100%;
    height: 100vh;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#6335e0+0,8e73d8+100 */
    background: #6335e0; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  #6335e0 0%,#8e73d8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6335e0', endColorstr='#8e73d8',GradientType=0 ); /* IE6-9 */
    color: white;
    text-align: center;
}

h1 {
    font-family: 'Courier New', Courier, monospace;
    color: rgba(0, 0, 0, 0.9);
    font-size: 70px;
    font-weight: 400;
    padding: 60px 0 40px 0;
}

h3 {
    font-weight: 400;
    font-size: 20px;
    padding: 0 0 40px 0;
}

img {
    width: 100px;
    height: 100px;
}
.layoutContainer {
    width: 85%;
    min-height: 80vh;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    border-radius: 40px;
    margin: 40px auto;
    padding: 50px;
    background-color: var(--gray-3);
}

.layoutContainer h2 {
    font-family: 'Aricum Variable Concept';
    font-size: 30px;
    margin: 0 0 0 20px;
    font-weight: bold;
}

.layoutContainer hr {
    width: 100%;
    height: 0;
    border: 2px black solid;
    margin: 10px  0;
}

.layoutContainer .cards {
    padding: 30px 0 0 20px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 50px 0;
}
.card {
    min-height: 250px;
    width: 250px;
    background-color: var(--gray-1);
    display: flex;
    flex-direction: column;
    border-radius: 30px;
    overflow: hidden;
    font-family: 'Aricum Variable Concept';
}

.card .colorDiv {
    width: 100%;
    height: 60%;
}

.card .data h3 {
    font-size: 25px;
    padding: 20px 0 0 20px;
}

.card .data p {
    font-size: 15px;
    font-weight: bold;
    padding: 5px 0 0 20px;
}

.card:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.card:hover .colorDiv {
    opacity: 0.9;
}

.card:active {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
}
.addNewCard {
    min-height: 250px;
    width: 250px;
    background-color: var(--gray-1);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
}

.addNewCard:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.addNewCard:active {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
}

.addNewCard .button {
    border: 6px var(--gray-4) solid;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    -webkit-user-select: none;
            user-select: none;
}

.addNewCard .button img {
    margin: auto;
    width: 60%;
    height: 60%;
}
.overlay {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    z-index: 1000000;
}

.createCard {
    position: relative;
    margin: auto;
    background-color: white;
    width: 60%;
    display: flex;
    flex-direction: column;
    padding: 30px;
    border-radius: 30px;
}

.createCard h1{
    font-family: 'Aricum Variable Concept';
    font-size: 25px;
    font-weight: bold;
    margin: auto;
    padding: 10px 0 0 0;
}

.createCard .close{
    width: 70px;
    height: 40px;
    display: flex;
    -webkit-user-select: none;
            user-select: none;
    right: 40px;
    top: 30px;
    position: absolute;
    transition: background-color 100ms ease;
    border-radius: 5px;
}

.createCard .close:hover {
    cursor: pointer;
    background-color: var(--gray-3);
}

.createCard .close:active {
    background-color: var(--gray-1);
}

.createCard .close img {
    width: 30px;
    height: 30px;
    margin: auto;
}

.createCard > form {
    width: 100%;
    box-shadow: none;
    border: none;
    font-family: 'Aricum Variable Concept';
    color: black;
    display: flex;
    margin: 0;
    padding: 10px;
}

.createForm .title::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
}

.createForm .title::placeholder {
    color: rgba(0, 0, 0, 0.8);
}

.createForm label {
    margin: 10px auto 0 0;
    font-size: 20px;
    padding: 0 10px;
}

.createForm #description {
    border: 4px var(--gray-2) solid;
    height: 100px;
    margin: 10px 0;
}

.createForm #description:focus{
    border: 4px var(--pantone-1) solid;
}

.createForm .colorDiv {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.createForm .colorDiv input:hover {
    cursor: pointer;
}

.createForm .colorDiv .color {
    width: 10%;
    height: 40px;
    margin: 10px;
    border-radius: 5px;
}

.createForm .colorDiv .color1 {
    background-color: var(--color-1);
}

.createForm .colorDiv .color2 {
    background-color: var(--color-2);
}

.createForm .colorDiv .color3 {
    background-color: var(--color-3);
}

.createForm .colorDiv .color4 {
    background-color: var(--color-4);
}

.createForm .colorDiv .color5 {
    background-color: var(--color-5);
}

.createForm .colorDiv .color6 {
    background-color: var(--color-6);
}

.createForm .colorDiv .color7 {
    background-color: var(--color-7);
}

.createForm button {
    margin: 10px 0;
}
.removeElement {
    min-height: 250px;
    width: 250px;
    background-color: var(--pantone-2);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
}

.removeElement > div {
    border: 6px white solid;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    -webkit-user-select: none;
            user-select: none;
}

.removeElement > div img {
    margin: auto;
    width: 60%;
    height: 60%;
}

.navbar {
    background-color: var(--pantone-1);
    padding: 20px;
    display: flex;
    justify-content: flex-start;
}

.navbar img {
    width: 50px;
    height: 50px;
    margin: 0 0 0 30px;
    padding: 0;
    border-radius: 50%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.text {
    margin: auto 0 auto 40px;
}

.text h1{
    font-family: 'Aricum Variable Concept';
    font-size: 35px;
    padding: 0;
    color: white;
    margin: auto;
}

.text h1 span {
    font-size: 40px;
    font-weight: bolder;
    color: var(--pantone-2);
}
